import {
  HashRouter as Router,
  Switch,
  Route,
  useHistory,
  useParams,
  Link,
  Redirect
} from "react-router-dom";

export default function App() {
  return (
    <Router basename="/">
      <div>
        <nav>
          <ul>
            <li>
              <Link to="/">Home</Link>
            </li>
            <li>
              <Link to="/about">About</Link>
            </li>
            <li>
              <Link to="/users">Users</Link>
            </li>
          </ul>
        </nav>

        {/* A <Switch> looks through its children <Route>s and
            renders the first one that matches the current URL. */}
        <Switch>
          <Route exact path="/about/:who">
            <About />
          </Route>
          <Route exact path="/users/:id">
            <Users />
          </Route>
          <Route exact path="/">
            <Home />
          </Route>
          <Route path="*">
            <NotFound />
          </Route>
        </Switch>
      </div>
    </Router>
  );
}

function Home() {
  return (
    <div>
      Home here
      <Link to="/about"> Go to About</Link>
    </div>
  )
}

function About() {
  const { who } = useParams();
  return (
    <div>
      {who === 'Olawale' && (
        <Redirect to='/users' />
      )}
      About here
    </div>
  )
}

function Users(props) {
  const history = useHistory();
  const { id } = useParams();

  console.log('user ID', id)
  return (
    <div>
      User here
      <button onClick={(e) => history.push('/about')}> Go to About</button>
    </div>
  )
}

function NotFound() {
  return <p>Not found</p>
}
